                :value="option.value" 
                v-model="filter_active_chinalev" 
              />
              {{ option.text }}
            </label>
          </div>
        </div> 
      </div> 
  
      <!-- Sortby Dropdown -->
      <div @click="handleClickOutside" class="dropdown-container">
        <div 
